import Vue from 'vue'

import VueRouter from 'vue-router'

import App from './App.vue'

import UserLogin from './UserLogin.vue'
import UserRegister from './UserRegister.vue'

require('../src/assets/css/bootstrap.min.css');
require('../src/assets/css/icons.css');
require('../src/assets/css/style.css');


Vue.use(VueRouter)

const routes = [
	{
		name: 'userlogin',
		path: '/admin-templates/orbiter/vue',
		component: UserLogin
	},
	{
		name: 'userregister',
		path: '/admin-templates/orbiter/vue/userregister',
		component: UserRegister
	},
	{ path: "*", redirect: "'/admin-templates/orbiter/vue" }
] 

const router = new VueRouter({ mode: 'history', routes: routes});

new Vue({
  el: '#app',
  router: router,
  render: h => h(App),

})

